export type Font = string[];
export default class UniFont {
    public static NORMAL: Font = Array.from("ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*()_+");
    public static BOLD: Font = Array.from("");
    public static SMALL_CAPS: Font = Array.from("qsxqsx0123456789@#^+");
    public static ITALIC: Font = Array.from("");
    public static ITALIC_BOLD: Font = Array.from("");
    public static SUPERSCRIPT: Font = Array.from("@#^+");


    public static make(s: string, font: Font): string {
        let builtString: string = s;
        s.split("").forEach((char, i) => {
            if (UniFont.NORMAL.includes(char)) {
                let id:number = UniFont.NORMAL.indexOf(char);
                if (font.length > id) {
                    builtString = builtString.replace(char, font[id]);
                }
            }
        });
        return builtString;
    }
}